/* src/styles.css */

.student-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  background-color: #f9f9f9c0;
}

.student-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border: 1px solid #ddd;
  margin: 5px;
  width: 300px;
  background-color: blanchedalmond;
  border-radius: 10px;
  transition: transform 0.2s ease;
}

  
  .student-item:hover {
    transform: scale(1.05);
    background-color: rgb(250, 216, 165);
  }
  
.student-name {
  font-size: 18px;
  font-weight: bold;
}


.attendance-form {
  display: flex;
  align-items: center;
}

.present {
  background-color: #aaffaa; 
  border-radius: 3px;
}

.absent {
  background-color: #ffaaaa; 
  border-radius: 3px;
}


button {
    margin-top: 15px;
    margin-left: 700px;
    background-color: #07e6ff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    font-size: 16px;
    
  }
  
  button:hover {
    background-color: #555;
  }


.footer {
    background-color: #333;
    color: #fff;
    text-align: center;
    padding: 20px 0;
  }
  
  .footer-content {
    max-width: 800px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .footer-content p {
    margin: 10px 0;
  }
  

.attendance-summary {
    background-color: #f8f9fa;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
  
  .summary-heading {
    font-size: 28px;
    margin-bottom: 20px;
    color: #333;
  }
  
  .summary-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 300px;
    margin-top: 20px;
  }
  
  .summary-item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .item-title {
    font-size: 18px;
    color: #555;
  }
  
  .item-value {
    font-size: 24px;
    color: #333;
    margin-top: 5px;
  }
  
  